import Vue from 'vue'
import Router from 'vue-router'
Vue.use(Router)
const router = new Router({
	mode: 'hash', //history or hash
	routes: [
		{
			path: '/',
			name: 'Home',
			component: () => import('@/components/Home.vue')
		},
		{
			path: '/allproject',
			name: 'AllProject',
			component: () => import('@/components/project/AllProject.vue')
		}
	]
});

export default router;
